@import url("common.scss");
.container {
  position: relative;
  width: 100%;
  height: 500px; }
  .container .wrapper {
    background: url("/img/bg.jpg") no-repeat center center;
    width: 100%;
    height: 100%;
    -webkit-animation: blurBg 2s;
            animation: blurBg 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    position: absolute; }
  .container .content {
    position: absolute;
    left: 50%;
    top: 40%;
    margin-left: -300px;
    width: 600px;
    height: 200px;
    text-align: center; }
    .container .content ul {
      display: block; }
      .container .content ul li {
        float: left;
        margin-right: 20px; }
        .container .content ul li a {
          -webkit-transition: color .5s;
          -o-transition: color .5s;
          transition: color .5s; }
        .container .content ul li a:hover {
          color: #34AAFD; }
    .container .content .ct-title {
      color: white;
      margin-top: -400px;
      opacity: 0; }
    .container .content .ct-menu-div {
      width: 100%;
      height: 150px; }
      .container .content .ct-menu-div .ct-menu-div-ct {
        margin: 20px auto;
        width: 300px;
        height: 150px;
        text-align: center; }
        .container .content .ct-menu-div .ct-menu-div-ct .ct-menu {
          margin-left: 40px; }
  .container .footer {
    width: 100%;
    height: 80px;
    position: absolute;
    bottom: 0;
    left: -900px;
    opacity: 0; }
    .container .footer .footer-ct {
      width: 135px;
      height: 70px;
      line-height: 70px;
      margin: 0 auto;
      font-size: 14px;
      color: #34AAFD; }

@-webkit-keyframes blurBg {
  from {
    -webkit-filter: blur(0);
            filter: blur(0); }
  to {
    -webkit-filter: blur(3px);
            filter: blur(3px); } }

@keyframes blurBg {
  from {
    -webkit-filter: blur(0);
            filter: blur(0); }
  to {
    -webkit-filter: blur(3px);
            filter: blur(3px); } }



